<template>
    <div v-if="status == 'waiting'" class="w-100 d-flex justify-content-center align-items-center">
        <div v-if="is_connected && game_code_exists" class="d-flex flex-column align-items-center">
            <div class="spinner-border" role="status" :style="{'color': $store.state.c4}">
                <span class="sr-only">Loading...</span>
            </div>
            <p class="text-center m-3" :style="{'color': $store.state.c4}"><i class="fa-solid fa-mug-hot"></i> Veuillez patienter</p>
            <Btn :text="'Annuler'" :is_sm="true" :c1="$store.state.c3" :c2="$store.state.c1" :cl="'fa-solid fa-rotate-left'" @click="quit_from_comp" />
        </div>
        <div v-else-if="is_connected && !game_code_exists" class="d-flex flex-column align-items-center">
            <p class="text-center m-3 font-weight-bold" :style="{'color': $store.state.cdanger}">Aucune partie ne correspond au code {{ game_code }}</p>
            <Btn :text="'Accueil'" :is_sm="true" :c1="$store.state.c3" :c2="$store.state.c1" :cl="'fa-solid fa-rotate-left'" @click="quit_from_comp" />
        </div>
        <div v-else class="d-flex flex-column align-items-center">
            <p class="text-center m-3 font-weight-bold" :style="{'color': $store.state.cdanger}">Une erreur s'est produite <i class="fa-solid fa-heart-crack"></i></p>
            <Btn :text="'Accueil'" :is_sm="true" :c1="$store.state.c3" :c2="$store.state.c1" :cl="'fa-solid fa-rotate-left'" @click="quit_from_comp" />
        </div>
        
    </div>
    <div v-else class="col h-100" :class="{'on-mobile': screen_width < 768, 'on-pc': screen_width >= 768}">
        <div class="row h-100">
            <div class="col-md-8 p-0" id="game-game">
                <PhaseStart v-if="phase == 'start'" :is_host="host" @start="change_phase" :game_code="game_code" :players="players" :data_game="data_game" :host_pseudo="host_pseudo" />
                <PhaseLoadingStart v-else-if="phase == 'loading_start'" :is_host="host" :round="round" />
                <PhaseQuestion v-else-if="phase == 'question'" :is_host="host" :q_a="q_a" :round="round" :data_phase="data_phase" @answer="send_answer" @end="handle_end" />
                <PhaseLoadingQuestion v-else-if="phase == 'loading_question'" :is_host="host" />
                <PhaseVote v-else-if="phase == 'vote'" 
                    :is_host="host" :q_a="q_a" 
                    :round="round" 
                    :data_phase="data_phase" 
                    :origin_sid="origin_sid" 
                    @vote="vote_for"
                    :can_vote="data_phase.voting_people.includes(origin_sid)"
                    @end="handle_end"
                    />
                <PhaseLoadingVote v-else-if="phase == 'loading_vote'" :is_host="host" />
                <PhaseResultVote v-else-if="phase == 'result_vote'" :is_host="host" :round="round" :data_phase="data_phase" :q_a="q_a" @end="handle_end" />
                <PhaseRanking v-else-if="phase == 'ranking'" :is_host="host" :round="round" :data_phase="data_phase" :players="players" @end="handle_end" />
            </div>


            <GameChat class="col-md-4 p-0" id="game-chat" @quit="quit_from_comp" :chat="chat.msg" :origin_sid="origin_sid" :game_code="game_code" :data_game="data_game" :host_pseudo="host_pseudo" :players="players" :is_host="host" :phase="phase" @message="send_message_chat" :load_chat="is_connected && load_chat" :index_1="index_1" :load_more_chat="is_connected && load_more_chat" 
                @more_chat="do_load_more_chat" />
        </div>
    </div>
</template>

<script>
// import axios from 'axios'
import $ from 'jquery'
import io from 'socket.io-client'
import Btn from '@/components/elements/Btn.vue'
import GameChat from '@/components/game/GameChat.vue'
import PhaseStart from '@/components/game/PhaseStart.vue'
import PhaseLoadingStart from '@/components/game/PhaseLoadingStart.vue'
import PhaseQuestion from '@/components/game/PhaseQuestion.vue'
import PhaseLoadingQuestion from '@/components/game/PhaseLoadingQuestion.vue'
import PhaseVote from '@/components/game/PhaseVote.vue'
import PhaseLoadingVote from '@/components/game/PhaseLoadingVote.vue'
import PhaseResultVote from '@/components/game/PhaseResultVote.vue'
import PhaseRanking from '@/components/game/PhaseRanking.vue'

export default {
    components: {
        Btn,
        GameChat,
        PhaseStart,
        PhaseLoadingStart,
        PhaseQuestion,
        PhaseLoadingQuestion,
        PhaseVote,
        PhaseLoadingVote,
        PhaseResultVote,
        PhaseRanking
    },
    props: {
        pseudo: {
            type: String,
            default: "Joueur"
        },
        is_host: {
            type: Boolean,
            default: true
        },
        game_code: {
            type: String,
            default: ''
        }
    },
    data () {
        return {
            status: "waiting",
            screen_width: 0,
            screen_height: 0,
            host: this.is_host,
            phase: "start",
            chat: {
                "msg": []
            },
            socket: null,
            is_connected: true,
            origin_sid: null,
            check_first_connection: '',
            game_code_exists: true,
            players: [],
            data_game: {},
            data_phase: {},
            host_pseudo: "Joueur",
            round: 1,
            q_a: {
                question: '',
                answer: ''
            },
            load_chat: false,
            load_more_chat: false,
            index_1: 0
        }
    },
    methods: {
        do_load_more_chat() {
            this.load_more_chat = true
            let data = {
                "game_code": this.game_code,
                "origin_sid": this.origin_sid,
                "first_load": false,
                "index_1": this.index_1
            }
            this.socket.emit("load_chat", JSON.stringify(data))
        },
        is_chat_scroll_bottom() {
            let content_chat_jq = $("#content-chat")
            let content_chat = document.getElementById("content-chat")
            let is_at_bottom = false
            if (content_chat) {
                let content_height = content_chat_jq.height()
                // if (content_chat.scrollTop + content_height == content_chat.scrollHeight) {
                if (content_chat.scrollHeight - (content_chat.scrollTop + content_height) <= 10) {
                    is_at_bottom = true
                }
            }
            return is_at_bottom
        },
        make_chat_scroll_bottom() {
            var content_chat = document.getElementById("content-chat")
            $("#content-chat").animate({ scrollTop: content_chat.scrollHeight }, "fast")
        },
        send_message_chat(data) {
            let salt = this.generate_chars()
            let data_msg = {
                "type": "msg",
                "from": this.origin_sid,
                "pseudo": this.pseudo,
                "value": data.message,
                "sent": true,
                "salt": salt
            }
            let data_emit = {
                "game_code": this.game_code,
                "from_sid": this.origin_sid,
                "data_msg": data_msg
            }
            this.socket.emit("send_message", JSON.stringify(data_emit))
            let data_msg_push = JSON.parse(JSON.stringify(data_msg))
            data_msg_push.sent = false
            this.chat.msg.push(data_msg_push)
            this.make_chat_scroll_bottom()
        },
        handle_end(data) {
            let can_change = false
            if (data == "loading_question" && this.phase == "question") {
                can_change = true
            }
            else if (data == "loading_vote" && this.phase == "vote") {
                can_change = true
            }
            else if (data == "ranking" && this.phase == "result_vote") {
                can_change = true
            }
            else if (data == "loading_start" && this.phase == "result_vote") {
                can_change = true
                this.round += 1
            }
            else if (data == "start" && this.phase == "ranking") {
                can_change = true
            }

            if (can_change) {
                this.phase = data
            }
        },
        vote_for(data) {
            let data_emit = {
                "game_code": this.game_code,
                "from_sid": this.origin_sid,
                "sid_to_vote": data.sid
            }
            this.socket.emit("vote_for", JSON.stringify(data_emit))
        },
        send_answer(data) {
            let data_emit = {
                "game_code": this.game_code,
                "from_sid": this.origin_sid,
                "answer": data.answer
            }
            this.socket.emit("send_answer", JSON.stringify(data_emit))
        },
        generate_chars() {
            let chars = ''
            const CHARS_LENGTH = 4
            const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789';
            const charactersLength = characters.length;
            let counter = 0;
            while (counter < CHARS_LENGTH) {
                chars += characters.charAt(Math.floor(Math.random() * charactersLength));
                counter += 1;
            }
            return chars
        },
        change_phase(data) {
            let f = data.from_phase
            if (f == 'start') {
                this.phase = data.phase
                if (this.host) {
                    this.round = 1
                    let data_emit = {
                        "game_code": this.game_code,
                        "from_sid": this.origin_sid,
                        "data_game": data.data_game
                    }
                    this.socket.emit("start_game", JSON.stringify(data_emit))
                }
            }
        },
        quit_from_comp() {
            let data = {
                "mode": "select_mode"
            }
            this.$emit('mode', data)
        },
        click_confirm() {
            let code = this.code.trim()
            this.code = code
            if (!code) {
                alert("Le code ne doit pas être vide")
            }
            else {
                this.$emit("confirm", code)
            }
        },
        go_home() {
            this.$emit("mode", "select_mode")
        },
        update_screen_size() {
            this.screen_width = window.screen.width
            this.screen_height = window.screen.height
        },
        before_window_unload(e) {
            e.preventDefault()
            let return_value = "Vous êtes sur le point de quitter cette partie"
            e.returnValue = return_value
            return return_value
        },
        handle_connect() {
            this.is_connected = true
            let chars = this.generate_chars()
            this.check_first_connection = chars
            let data = {
                "game_code": this.game_code,
                "is_host": this.is_host,
                "origin_sid": this.origin_sid,
                "pseudo": this.pseudo,
                "chars": chars
            }
            this.socket.emit("join_game", JSON.stringify(data))
        },
        handle_disconnect() {
            this.is_connected = false
            this.host = false
            let is_at_bottom = this.is_chat_scroll_bottom()
            this.chat.msg.push({
                "type": "disco",
                "from": this.origin_sid,
                "pseudo": this.pseudo
            })
            if (is_at_bottom) {
                this.make_chat_scroll_bottom()
            }
        },
        update_data(msg) {
            this.players = msg.data.people
            this.data_game = msg.data.data_game
            this.data_phase = msg.data.data_phase
            this.host_pseudo = msg.data.host_pseudo
            this.q_a = {
                question: msg.data.question,
                answer: msg.data.answer,
            }
            this.round = msg.data.round
            this.phase = msg.data.phase
        },
        handle_message(msg) {
            msg = JSON.parse(msg)
            console.log(msg)
            if (msg.type == "res_join_game") {
                if (msg.success) {
                    if (msg.chars == this.check_first_connection) {
                        this.origin_sid = msg.origin_sid
                    }
                    this.check_first_connection = null
                    this.update_data(msg)
                    this.status = "game"

                    // Load the chat if I'm the one who has joined the game
                    if (this.origin_sid == msg.origin_sid) {
                        this.load_chat = true
                        let data = {
                            "game_code": this.game_code,
                            "origin_sid": this.origin_sid,
                            "first_load": true
                        }
                        this.socket.emit("load_chat", JSON.stringify(data))
                    }
                }
                else {
                    this.game_code_exists = false
                    this.host = false
                    this.load_chat = false
                    let is_at_bottom = this.is_chat_scroll_bottom()
                    this.chat.msg.push({
                        "type": "game_not_found"
                    })
                    if (is_at_bottom) {
                        this.make_chat_scroll_bottom()
                    }
                }
            }
            else if (msg.type == "res_start_game") {
                if (msg.success) {
                    this.update_data(msg)
                }
            }
            else if (msg.type == "res_next_phase") {
                this.update_data(msg)
            }
            else if (msg.type == "res_send_message") {
                if (msg.data.type == "msg" && msg.data.from == this.origin_sid) {
                    for (var i = this.chat.msg.length -1; i >= 0; i--) {
                        let m = this.chat.msg[i]
                        if (msg.data.from == m.from && msg.data.salt == m.salt) {
                            this.chat.msg[i].sent = true
                            break
                        }
                    }
                }
                else {
                    let is_at_bottom = this.is_chat_scroll_bottom()
                    this.chat.msg.push(msg.data)
                    if (is_at_bottom) {
                        this.make_chat_scroll_bottom()
                    }
                }
            }
            else if (msg.type == "res_disconnection") {
                this.players = msg.people
                if (msg.new_host) {
                    this.host_pseudo = msg.host_pseudo
                    if (msg.host_sid == this.origin_sid) {
                        this.host = true
                    }
                }
            }
            else if (msg.type == "res_load_chat") {
                if (msg.success) {
                    if (msg.origin_sid == this.origin_sid) {
                        this.index_1 = msg.index_1
                        if (msg.first_load) {
                            this.chat.msg = msg.chat
                            // Make scroll bottom
                            var content_chat = document.getElementById("content-chat")
                            content_chat.scrollTop = content_chat.scrollHeight
                            this.make_chat_scroll_bottom()
                        }
                        else {
                            this.chat.msg = msg.chat.concat(this.chat.msg)
                        }
                    }
                }
                this.load_chat = false
                this.load_more_chat = false
            }
        }
    },
    created() {
        this.$store.dispatch('set_is_in_game', true)
        window.addEventListener('beforeunload', this.before_window_unload)

        this.socket = io(this.$store.state.url_b_socket)
        
        this.socket.on('connect', () => {
            console.log('Connecté au serveur Socket.IO')
            this.handle_connect()
        });

        this.socket.on('disconnect', () => {
            console.log('Déconnecté du serveur Socket.IO')
            this.handle_disconnect()
        });

        this.socket.on('connect_error', () => {
            this.is_connected = false
        });

        this.socket.on('message', this.handle_message)
    },
    mounted() {
        this.update_screen_size()
        window.addEventListener('resize', this.update_screen_size)
    },
    beforeUnmount() {
        window.removeEventListener('resize', this.update_screen_size)
        this.$store.dispatch('set_is_in_game', false)
        window.removeEventListener('beforeunload', this.before_window_unload)

        if (this.socket) {
            this.socket.disconnect()
        }
    }
}
</script>

<style scoped>
.on-pc #game-game {
    height: 100%;
}

.on-pc #game-chat {
    height: 100%;
}

.on-mobile #game-game {
    height: 50%;
}

.on-mobile #game-chat {
    height: 50%;
}

#game-game {
    overflow: auto;
}

</style>