<template>
    <div class="container h-100">
        <div id="home-top" class="row">
            <div class="col d-flex justify-content-center align-items-center">
                <h4 class="text-center font-1" :style="{'color': $store.state.c4}">~ Words Against Humanity ~</h4>
            </div>
        </div>

        <div id="home-body" class="row">
            <SelectMode v-if="mode == 'select_mode'" @mode="change_mode" :pseudo="pseudo" @pseudo="change_pseudo" />
            <JoinGame v-else-if="mode == 'join'" @confirm="join_game" @mode="change_mode" :pseudo="pseudo" />
            <Game v-else-if="mode == 'game'" @mode="change_mode" :game_code="game_code" :pseudo="pseudo" :is_host="is_host" />
        </div>
    </div>
</template>

<script>
import SelectMode from '@/components/home/SelectMode.vue'
import JoinGame from '@/components/home/JoinGame.vue'
import Game from '@/components/home/Game.vue'

export default {
    components: {
        SelectMode,
        JoinGame,
        Game
    },
    data () {
        return {
            mode: 'select_mode',
            pseudo: 'Joueur',
            game_code: '',
            is_host: true
        }
    },
    methods: {
        change_mode(data) {
            if (data.mode == "game") {
                this.game_code = data.game_code
                this.is_host = true
            }
            this.mode = data.mode
        },
        change_pseudo(pseudo) {
            this.pseudo = pseudo
        },
        join_game(game_code) {
            this.game_code = game_code.toUpperCase()
            this.is_host = false
            this.mode = "game"
        }
    },
    created() {
        let pseudo = localStorage.getItem("wah_pseudo")
        if (pseudo) {
            this.pseudo = pseudo
        }
    }
}
</script>

<style scoped>
#home-top{
    height: 8%;
}

#home-body{
    height: 92%;
    overflow: auto;
}
</style>