<template>
    <div class="indication-bar d-flex justify-content-between px-1" :style="{'background-color': $store.state.c2}">
        <div class="indication-time" :style="{'color': $store.state.c4}">
            <i class="fa-solid fa-hourglass-half"></i> {{time_left}}
        </div>

        <div class="indication-phase mx-2" :style="{'color': $store.state.c4}">
            <p v-if="indication == 'question'" class="text-center m-0"><i class="fa-regular fa-circle-question"></i> Question</p>
            <p v-else-if="indication == 'vote'" class="text-center m-0"><i class="fa-solid fa-check-to-slot"></i> Vote</p>
            <p v-else-if="indication == 'result_vote'" class="text-center m-0"><i class="fa-solid fa-square-poll-vertical"></i> Résultat des votes</p>
            <p v-else-if="indication == 'ranking'" class="text-center m-0"><i class="fa-solid fa-ranking-star"></i> Classement</p>
        </div>

        <div class="indication-round" :style="{'color': $store.state.c4}">
            <p class="text-center m-0 font-italic">Round {{ round }}</p>
        </div>
    </div>
</template>

<script>
// import axios from 'axios'
// import Btn from '@/components/elements/Btn.vue'

export default {
    components: {
        // Btn
    },
    props: {
        indication: {
            type: String,
            default: 'question'
        },
        date_end: {
            default: null
        },
        round: {
            type: Number,
            default: 1
        }
    },
    data () {
        return {
            timer: null,
            date_now: new Date(Date.UTC())
        }
    },
    computed: {
        time_left() {
            let date_end = new Date(this.date_end)
            const diffTime = date_end - this.date_now
            const diffSeconds = Math.ceil(diffTime / (1000))
            let second_left = ''
            if (Number.isInteger(diffSeconds)) {
                second_left = diffSeconds
                if (diffSeconds < 0) {
                    second_left = 0
                    this.$emit("end")
                    this.set_timeout()
                }
            }
            return second_left
        }
    },
    methods: {
        set_timeout() {
            setTimeout(this.timer)
        }
    },
    created() {
        this.timer = setInterval(function(){
            this.date_now = new Date()
        }.bind(this), 1000)
    },
    beforeUnmount() {
        setTimeout(this.timer)
    }
}
</script>

