import { createStore } from 'vuex'

export default createStore({
  state: {
    token: "loading",
    url_b: 'http://' + window.location.hostname + ':5000',
    // url_b_socket: 'http://' + window.location.hostname + ':5000',
    url_b_socket: 'https://north-dynamic-produce.glitch.me',
    c1: '#27374D',
    c2: '#526D82',
    c3: '#9DB2BF',
    c4: '#DDE6ED',
    cdanger: '#DC3545',
    csuccess: '#28a745',
    cwarning: '#A73121',
    cpoints: '#28a745',
    is_in_game: false
  },
  mutations: {
    SET_TOKEN(state, payload) {
      state.token = payload
    },
    SET_IS_IN_GAME(state, payload) {
      state.is_in_game = payload
    }
  },
  actions: {
    set_token(context, token) {
      context.commit('SET_TOKEN', token)
    },
    set_is_in_game(context, is_in) {
      context.commit('SET_IS_IN_GAME', is_in)
    }
  },
  modules: {
  }
})
