<template>
    <div class="p-1 h-100">
        <div class="h-100">
            <IndicationBar :indication="'vote'" :date_end="data_phase.date_end" :round="round" @end="$emit('end', 'loading_vote')" />

            <div class="vote-zone p-1 pb-2" :style="{'color': $store.state.c4}">
                <p class="text-center m-0">{{ q_a.question }}</p>

                <p class="text-center m-0 my-1">Vote pour la réponse <span class="font-weight-bold">{{ q_a.answer }}</span></p>

                <p class="text-center m-0 my-1 font-italic">(Tu ne peux voter que pour celle des autres joueurs)</p>

                <div v-if="data_phase.answers_list.length > 0" class="pt-2">
                    <div v-for="ans, i in data_phase.answers_list" :key="i" class="mb-2 p-1 rounded" :style="{'background-color': $store.state.c3}">
                        <p class="m-0 font-italic" :style="{'color': $store.state.c1}"><span class="answer-num font-weight-bold">Réponse n°{{ i+1 }}</span></p>
                        <p class="m-0 answer-text pb-2 px-2 text-center" :style="{'color': $store.state.c1}">{{ ans.answer }}</p>
                        <div v-if="can_vote && ans.sid != origin_sid" class="m-0 d-flex justify-content-end">
                            <Btn v-if="!my_vote" :text="'Je vote pour ceci'" :is_sm="true" :c1="$store.state.c4" :c2="$store.state.c1" :cl="'fa-regular fa-thumbs-up'" @click="vote_for(ans.sid)" />
                            <Btn v-else-if="my_vote == ans.sid" 
                                :text="'J\'ai voté pour ceci'" 
                                :is_sm="true" :c1="$store.state.c4" 
                                :c2="$store.state.c1" 
                                :cl="'fa-solid fa-thumbs-up'"
                                :disabled="true"
                                />
                        </div>
                        <div v-else-if="!can_vote" class="m-0 d-flex justify-content-end">
                            <span class="font-italic" :style="{'color': $store.state.cdanger}">Vous ne pouvez pas voter</span>
                        </div>
                        <div v-else class="m-0 d-flex justify-content-end">
                            <span class="font-italic" :style="{'color': $store.state.c2}"><i class="fa-solid fa-hand-point-up"></i> Votre réponse</span>
                        </div>
                    </div>
                </div>
                <div v-else class="pt-2">
                    <p class="text-center m-0 font-weight-bold" :style="{'color': $store.state.cdanger}">
                        Personne n'a donné de réponse <i class="fa-regular fa-face-frown"></i>
                    </p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// import axios from 'axios'
import Btn from '@/components/elements/Btn.vue'
import IndicationBar from '@/components/game/IndicationBar.vue'

export default {
    components: {
        IndicationBar,
        Btn
    },
    props: {
        is_host: {
            type: Boolean,
            default: true
        },
        q_a: {
            default: {
                "question": "",
                "answer": ""
            }
        },
        round: {
            type: Number,
            default: 1
        },
        data_phase: {
            default: {}
        },
        origin_sid: {
            type: String,
            default: ''
        },
        can_vote: {
            type: Boolean,
            default: false
        }
    },
    data () {
        return {
            my_vote: null
        }
    },
    methods: {
        vote_for(sid) {
            this.my_vote = sid
            let data = {
                "sid": sid
            }
            this.$emit("vote", data)
        }
    },
}
</script>

<style scoped>
.vote-zone p, .vote-zone span {
    font-size: 14px;
}

.answer-num {
    font-size: 12px;
}
</style>