<template>
    <div>
        <ul class="nav nav-tabs">
            <li class="nav-item" @click="tab_focus='chat'">
                <a class="nav-link active" href="#tab-content-chat" data-toggle="tab" 
                    :style="{'background-color': tab_focus == 'chat' ? $store.state.c4 : $store.state.c1, 'color': tab_focus == 'chat' ? $store.state.c1 : $store.state.c4}"
                >
                    <i class="fa-solid fa-comment"></i>
                </a>
            </li>
            <li class="nav-item" @click="tab_focus='ranking'">
                <a class="nav-link" href="#tab-content-ranking" data-toggle="tab" 
                    :style="{'background-color': tab_focus == 'ranking' ? $store.state.c4 : $store.state.c1, 'color': tab_focus == 'ranking' ? $store.state.c1 : $store.state.c4}"
                >
                    <i class="fa-solid fa-ranking-star"></i>
                </a>
            </li>
            <li class="nav-item" @click="click_tab_config()">
                <a class="nav-link" href="#tab-content-config" data-toggle="tab" 
                    :style="{'background-color': tab_focus == 'config' ? $store.state.c4 : $store.state.c1, 'color': tab_focus == 'config' ? $store.state.c1 : $store.state.c4}"
                >
                    <i class="fa-solid fa-gear"></i>
                </a>
            </li>
        </ul>

        <div class="tab-content mb-3" :style="{'background-color': $store.state.c4}">
            <div class="tab-pane active h-100" id="tab-content-chat">

                <div v-if="load_chat" id="chat-loader" class="d-flex flex-column justify-content-center align-items-center" :style="{'background-color': $store.state.c4}">
                    <div class="spinner-border" role="status" :style="{'color': $store.state.c1}">
                        <span class="sr-only">Loading...</span>
                    </div>
                    <p class="text-center m-2 font-weight-bold" :style="{'color': $store.state.c1}">Chargement des messages</p>
                </div>


                <div class="h-100" id="tab-content-chat-container">
                    <div class="p-1" id="content-chat">
                        <div v-if="index_1 > 0" class="d-flex justify-content-center mb-2">
                            <button class="btn btn-sm" :style="{'background-color': $store.state.c3, 'color': $store.state.c1}" :disabled="load_more_chat" @click="$emit('more_chat')">
                                <span v-if="load_more_chat" class="spinner-border spinner-border-sm"></span>
                                <i v-if="!load_more_chat" class="fa-solid fa-plus"></i>
                                <span v-if="load_more_chat"> Chargement...</span>
                                <span v-if="!load_more_chat"> Charger plus</span>
                            </button>
                        </div>

                        <div v-for="msg, i in chat" :key="i" class="">
                            <div v-if="msg.type == 'msg'" class="message-block-msg">
                                <p class="m-0" :class="{'msg-not-sent': !msg.sent}">
                                    <span class="font-weight-bold pl-1 mr-1" :style="{'color': msg.from == origin_sid ? $store.state.c4 : $store.state.c2, 'background-color': msg.from == origin_sid ? $store.state.c2 : 'none'}">{{ msg.pseudo }} <span v-if="msg.from == origin_sid">(Vous)</span> : </span>
                                    <span :style="{'color': $store.state.c1}">{{ msg.value }}</span>
                                </p>
                            </div>

                            <div v-else-if="msg.type == 'disco'" class="">
                                <p class="m-0 text-center font-italic font-weight-bold" :style="{'color': $store.state.cdanger}">
                                    {{ msg.from == origin_sid ? 'Vous vous êtes déconnecté' : msg.pseudo + " s'est déconnecté" }}
                                </p>
                            </div>

                            <div v-else-if="msg.type == 'reco'" class="">
                                <p class="m-0 text-center font-italic font-weight-bold" :style="{'color': $store.state.csuccess}">
                                    {{ msg.from == origin_sid ? 'Vous vous êtes connecté' : msg.pseudo + " s'est connecté" }}
                                </p>
                            </div>

                            <div v-else-if="msg.type == 'vote'" class="">
                                <p class="m-0 text-center font-italic font-weight-bold" :style="{'color': $store.state.cwarning}">
                                    <i class="fa-solid fa-check-to-slot"></i>
                                    {{ msg.from == origin_sid ? 'Vous avez voté' : msg.pseudo + " a voté" }}
                                    <span>({{ msg.meta.nb }}/{{ msg.meta.total }})</span>
                                </p>
                            </div>

                            <div v-else-if="msg.type == 'answer'" class="">
                                <p class="m-0 text-center font-italic font-weight-bold" :style="{'color': $store.state.cwarning}">
                                    <i class="fa-solid fa-check"></i>
                                    {{ msg.from == origin_sid ? 'Vous avez répondu' : msg.pseudo + " a répondu" }}
                                    <span>({{ msg.meta.nb }}/{{ msg.meta.total }})</span>
                                </p>
                            </div>

                            <div v-else-if="msg.type == 'new_host'" class="">
                                <p class="m-0 text-center font-italic font-weight-bold" :style="{'color': $store.state.cwarning}">
                                    <i class="fa-regular fa-hand-point-right"></i>
                                    {{ msg.to == origin_sid ? 'Vous êtes désigné comme nouvel hôte de la partie' : msg.pseudo + " est désigné comme nouvel hôte de la partie" }}
                                </p>
                            </div>

                            <div v-else-if="msg.type == 'game_not_found'" class="">
                                <p class="m-0 my-3 text-center font-italic font-weight-bold" :style="{'color': $store.state.c4, 'background-color': $store.state.cwarning}">
                                    La partie n'existe plus. Veuillez en créer une autre ou en rejoindre
                                </p>
                            </div>
                        </div>
                    </div>
                    <div class="input-group" id="input-group-chat">
                        <input type="text" class="form-control" placeholder="Votre message..." autocomplete="off" v-model="message_input" @keyup.enter="send_message()">
                        <Btn :is_sm="true" :cl="'fa-brands fa-rocketchat'" :c1="$store.state.c2" :c2="$store.state.c4" @click="send_message()" />
                    </div>
                </div>
            </div>

            <div class="tab-pane" id="tab-content-ranking">
                <div class="">
                    <p class="m-2 text-center font-weight-bold" :style="{'color': $store.state.c1}">Classement</p>
                    <div v-for="player,i in players" :key="i" class="d-flex justify-content-between rounded m-2" :style="{'border': '2px solid '+$store.state.c2}">
                        <div class="d-flex justify-content-center align-items-center ranking-row-rank font-weight-bold" :style="{'color': $store.state.c4, 'background-color': $store.state.c2}"><span>{{ '#'+(i+1) }}</span></div>
                        <div class="ranking-row-pseudo p-2">
                            <p class="m-0" :style="{'color': $store.state.c1}">
                                {{ player.pseudo }} <span v-if="origin_sid == player.sids[0]" class="font-italic font-weight-bold" :style="{'color': $store.state.cwarning}">(Vous)</span>
                            </p>
                            <p class="m-0">
                                <Award v-for="award, i in player.awards" :key="i" :for_what="award" />
                            </p>
                        </div>
                        <div class="d-flex justify-content-center align-items-center ranking-row-points font-weight-bold" :style="{'color': $store.state.c2, 'background-color': $store.state.c4}">
                            <span class="text-center">{{player.score}} {{player.score > 1 ? 'points' : 'point'}}</span>
                        </div>
                    </div>
                </div>
            </div>

            <div class="tab-pane p-1" id="tab-content-config">
                <div class="pt-2 d-flex justify-content-center">
                    <Btn v-if="!wanna_quit" :is_sm="true" :cl="'fa-solid fa-door-open'" :c1="$store.state.cdanger" :c2="$store.state.c4" :text="'Quitter'" @click="try_quit()" />
                    <div v-else class="">
                        <p class="text-center mb-2">Voulez-vous vraiment quitter la partie ?</p>
                        <div class="d-flex justify-content-center">
                            <Btn :is_sm="true" :c1="$store.state.c3" :c2="$store.state.c1" :text="'Non'" class="mx-2" @click="wanna_quit = false" />
                            <Btn :is_sm="true" :c1="$store.state.cdanger" :c2="$store.state.c4" :text="'Oui'" class="mx-2" @click="quit()" />
                        </div>
                    </div>
                </div>

                <p class="my-2" :style="{'color': $store.state.c1}">
                    Code de la partie : 
                    <Btn v-if="!code_copied" :is_sm="true" :text="code" :cl="'fa-solid fa-copy'" :c1="$store.state.c3" :c2="$store.state.c1" @click="copy_code()" />
                    <Btn v-else :is_sm="true" :text="'copié'" :cl="'fa-solid fa-check'" :c1="$store.state.c3" :c2="$store.state.c1" :is_disabled="true" />
                </p>

                <ul :style="{'color': $store.state.c1}">
                    <li>
                        <span v-if="is_host">Vous êtes l'hôte de la partie</span>
                        <span v-else><span class="font-weight-bold">{{host_pseudo}}</span> est l'hôte de la partie</span>
                    </li>
                    <li v-if="phase != 'start'" class="">
                        La partie se termine quand un joueur atteint <span class="font-weight-bold">{{ data_game.score_goal }} {{ data_game.score_goal > 1 ? 'points' : 'point' }}</span>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
// import axios from 'axios'
import Btn from '@/components/elements/Btn.vue'
import Award from '@/components/elements/Award.vue'

export default {
    components: {
        Btn,
        Award
    },
    props: {
        is_host: {
            type: Boolean,
            default: true
        },
        chat: {
            default: []
        },
        origin_sid: {
            type: String,
            default: ''
        },
        data_game: {
            default: {}
        },
        game_code: {
            type: String,
            default: ''
        },
        host_pseudo: {
            type: String,
            default: "Joueur"
        },
        players: {
            default: []
        },
        phase: {
            type: String,
            default: "start"
        },
        load_chat: {
            type: Boolean,
            default: false
        },
        index_1: {
            type: Number,
            default: 0
        },
        load_more_chat: {
            type: Boolean,
            default: false
        }
    },
    data () {
        return {
            tab_focus: 'chat',
            code_copied: false,
            code: this.game_code,
            timer_copy_code: null,
            wanna_quit: false,
            message_input: '',
            MESSAGE_MAX_LENGTH: 1000
        }
    },
    methods: {
        send_message() {
            this.message_input = this.message_input.trim()
            if (this.message_input) {
                if (this.message_input.length > this.MESSAGE_MAX_LENGTH) {
                    alert("Le message ne doit pas dépasser " + this.MESSAGE_MAX_LENGTH + " caractères")
                }
                else {
                    this.$emit("message", {"message": this.message_input})
                    this.message_input = ''
                }
            }
        },
        make_code_copied_false_in_moment() {
            this.timer_copy_code = setTimeout(function(){
                this.code_copied = false
            }.bind(this), 2000)
        },
        copy_code() {
            this.code_copied = true
            this.make_code_copied_false_in_moment()
            if (!navigator.clipboard) {
                const textArea = document.createElement("textarea");
                textArea.value = this.code;
                    
                // Maka textarea opacity 0 so it's not visible
                textArea.style.position = "absolute";
                textArea.style.opacity = 0;
                    
                document.body.prepend(textArea);
                textArea.select();

                try {
                    document.execCommand('copy');
                } catch (error) {
                    console.error(error);
                } finally {
                    textArea.remove();
                }
            }
            else {
                navigator.clipboard.writeText(this.code)
            }
        },
        try_quit() {
            this.wanna_quit = true
        },
        quit() {
            this.$emit('quit')
        },
        click_tab_config() {
            this.tab_focus = 'config'
            this.wanna_quit = false
        }
    },
    created() {
        
    }
}
</script>

<style scoped>

#game-chat {
    overflow: auto;

    display: flex;
    flex-flow: column;
}

/* #game-chat ul.nav-tabs {
    
} */

#game-chat .tab-content {
    flex-grow : 1;
    overflow: auto;
}

/* #game-chat .tab-content #tab-content-chat {
    display: flex;
    flex-flow: column;
} */

#game-chat .tab-content #tab-content-chat #tab-content-chat-container {
    display: flex;
    flex-flow: column;
}

#game-chat .tab-content #tab-content-chat #tab-content-chat-container #content-chat {
    flex-grow : 1;
    overflow: auto;
}


/* #game-chat .tab-content #tab-content-chat #input-group-chat {
    position: absolute;
    bottom: 0px;
    left: 0px;
    width: 100%;
} */


.nav-tabs {
    border-bottom: none;
}

.ranking-row-rank {
    width: 15%;
}

.ranking-row-pseudo {
    width: 60%;
}

.ranking-row-points {
    width: 25%;
}

#content-chat p {
    font-size : 13px;
}

.msg-not-sent {
    opacity: 0.4;
}

.message-block-msg {
    word-break: break-word;
}

#tab-content-chat {
    position: relative;
}

#chat-loader {
    position: absolute;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
</style>