<template>
    <div class="p-1">
        <div v-if="is_host" class="d-flex justify-content-center">
            <Btn :text="'Lancer'" :c1="$store.state.c2" :c2="$store.state.c4" :is_sm="true" :cl="'fa-solid fa-rocket'" @click="click_start()" />
        </div>
        <div v-else class="">
            <p class="m-0 text-center font-italic" :style="{'color': $store.state.cdanger}">Seul l'hôte peut lancer la partie</p>
        </div>
        <div class="">
            <span :style="{'color': $store.state.c4}">
                <i class="fa-solid fa-people-group"></i> Joueurs : <span>{{players.length}}</span>
            </span>
            <p class="" :style="{'color': $store.state.c4}">
                Code de la partie : 
                <Btn v-if="!code_copied" :is_sm="true" :text="code" :cl="'fa-solid fa-copy'" :c1="$store.state.c3" :c2="$store.state.c1" @click="copy_code()" />
                <Btn v-else :is_sm="true" :text="'copié'" :cl="'fa-solid fa-check'" :c1="$store.state.c3" :c2="$store.state.c1" :is_disabled="true" />
            </p>

            <div v-if="is_host" class="" :style="{'color': $store.state.c4}">
                <div class="form-group">
                    <label for="score-goal-input"><i class="fa-solid fa-flag-checkered"></i> Fin de la partie quand un joueur atteint le score :</label>
                    <input type="number" class="form-control" id="score-goal-input" min="1" v-model="d_game.score_goal">
                </div>
            </div>

            <div class="" :style="{'color': $store.state.c4}">
                <p v-if="is_host" class="m-0"><i class="fa-solid fa-house-user"></i> Vous êtes l'hôte de cette partie</p>
                <p v-else class="m-0"><i class="fa-solid fa-house-user"></i> <span class="font-weight-bold">{{ host_pseudo }}</span> est l'hôte de cette partie</p>
            </div>
        </div>
    </div>
</template>

<script>
// import axios from 'axios'
import Btn from '@/components/elements/Btn.vue'

export default {
    components: {
        Btn
    },
    props: {
        is_host: {
            type: Boolean,
            default: true
        },
        game_code: {
            type: String,
            default: ''
        },
        players: {
            default: []
        },
        data_game: {
            default: {}
        },
        host_pseudo: {
            type: String,
            default: "Joueur"
        }
    },
    data () {
        return {
            code_copied: false,
            code: this.game_code,
            timer_copy_code: null,
            d_game: this.data_game
        }
    },
    methods: {
        make_code_copied_false_in_moment() {
            this.timer_copy_code = setTimeout(function(){
                this.code_copied = false
            }.bind(this), 2000)
        },
        copy_code() {
            this.code_copied = true
            this.make_code_copied_false_in_moment()
            if (!navigator.clipboard) {
                const textArea = document.createElement("textarea");
                textArea.value = this.code;
                    
                // Maka textarea opacity 0 so it's not visible
                textArea.style.position = "absolute";
                textArea.style.opacity = 0;
                    
                document.body.prepend(textArea);
                textArea.select();

                try {
                    document.execCommand('copy');
                } catch (error) {
                    console.error(error);
                } finally {
                    textArea.remove();
                }
            }
            else {
                navigator.clipboard.writeText(this.code)
            }
        },
        click_start() {
            let score_goal = this.d_game.score_goal
            if (this.players.length < 3) {
                alert("Le nombre de joueurs doit être au moins 3")
            }
            else if (!Number.isInteger(score_goal) || score_goal < 1) {
                alert("Le score objectif doit être un nombre entier strictement supérieur à 0")
            }
            else {
                let data = {
                    "phase": "loading_start",
                    "from_phase": "start",
                    "data_game": this.d_game
                }
                this.$emit("start", data)
            }
        }
    },
}
</script>

<style scoped>
#score-goal-input {
    width: 100px;
}
</style>