<template>
    <div class="col">
        <div class="">
            <div class="form-group row">
                <div class="col-md-4"></div>
                <div class=" col-md-4">
                    <label for="pseudo" :style="{'color': $store.state.c4}">Votre pseudo</label>
                    <input type="text" class="form-control" id="pseudo" autocomplete="off" v-model="p" @input="input_change_pseudo()">
                </div>
                <div class="col-md-4"></div>
            </div>

            <div class="row d-flex justify-content-center">
                <div class="col-md-4">
                    <Btn class="btn-block" :c1="$store.state.c2" :c2="$store.state.c4" :text="'Rejoindre une partie'" :cl="'fa-solid fa-right-to-bracket'" @click="click_change_mode('join')" />
                    <Btn class="btn-block" :c1="$store.state.c2" :c2="$store.state.c4" :text="'Créer une partie'" :cl="'fa-solid fa-plus'" @click="click_change_mode('game')" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// import axios from 'axios'
import Btn from '@/components/elements/Btn.vue'

export default {
    components: {
        Btn
    },
    props: {
        pseudo: {
            type: String,
            default: "Joueur"
        }
    },
    data () {
        return {
            p: this.pseudo,
            MAX_PSEUDO_LENGTH: 20
        }
    },
    methods: {
        click_change_mode(mode) {
            let pseudo = this.p.trim()
            if (!pseudo || pseudo.length > this.MAX_PSEUDO_LENGTH) {
                alert("Pseudo entre 1 et " + this.MAX_PSEUDO_LENGTH + " caractères")
            }
            else {
                let game_code = ''
                const GAME_CODE_LENGTH = 8
                if (mode == "game") {
                    // Create game code
                    const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789';
                    const charactersLength = characters.length;
                    let counter = 0;
                    while (counter < GAME_CODE_LENGTH) {
                        game_code += characters.charAt(Math.floor(Math.random() * charactersLength));
                        counter += 1;
                    }
                }
                
                let data = {
                    "mode": mode,
                    "game_code": game_code
                }
                this.$emit("mode", data)
                localStorage.setItem("wah_pseudo", pseudo)
            }
        },
        input_change_pseudo() {
            this.$emit("pseudo", this.p)
        }
    },
}
</script>

