<template>
    <div class="p-1 h-100">
        <div class="h-100">
            <IndicationBar :indication="'question'" :date_end="data_phase.date_end" :round="round" @end="$emit('end', 'loading_question')" />

            <div class="question-zone p-1 pb-2" :style="{'color': $store.state.c4}">
                <p class="text-center m-0">{{q_a.question}}</p>

                <p class="text-center m-0 my-1">Donne la réponse <span class="font-weight-bold">{{q_a.answer}}</span></p>

                <div class="form-group m-0 mt-3">
                    <textarea class="form-control" rows="2" placeholder="Votre réponse..." v-model="answer" :disabled="answer_sent"></textarea>
                </div>

                <div class="d-flex justify-content-center p-1">
                    <Btn v-if="!answer_sent" :text="'Confirmer'" :is_sm="true" :cl="'fa-regular fa-thumbs-up'" :c1="$store.state.c2" :c2="$store.state.c4" @click="confirm_answer()" />
                    <Btn v-else :text="'Patientez jusqu\'aux votes'" :is_sm="true" :cl="'fa-solid fa-circle-check'" :c1="$store.state.c2" :c2="$store.state.c4" @click="confirm_answer()" :disabled="answer_sent" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// import axios from 'axios'
import Btn from '@/components/elements/Btn.vue'
import IndicationBar from '@/components/game/IndicationBar.vue'

export default {
    components: {
        IndicationBar,
        Btn
    },
    props: {
        is_host: {
            type: Boolean,
            default: true
        },
        q_a: {
            default: {
                "question": "",
                "answer": ""
            }
        },
        round: {
            type: Number,
            default: 1
        },
        data_phase: {
            default: {}
        }
    },
    data () {
        return {
            answer: '',
            ANSWER_MAX_LENGTH: 200,
            answer_sent: false
        }
    },
    methods: {
        confirm_answer() {
            this.answer = this.answer.trim()
            if (!this.answer || this.answer.length > this.ANSWER_MAX_LENGTH) {
                alert("La réponse doit être entre 1 et " + this.ANSWER_MAX_LENGTH + " caractères")
            }
            else {
                this.answer_sent = true
                let data = {
                    "answer": this.answer
                }
                this.$emit("answer", data)
            }
        }
    },
}
</script>

<style scoped>
.question-zone p {
    font-size: 14px;
}

textarea {
    resize: none;
    font-size: 14px;
    word-wrap:break-word
}
</style>