<template>
  <div id="theapp" class="h-100" :style="{'background-color': $store.state.c1}">
    <router-view :key="$route.fullPath" />
  </div>
</template>

<script>
export default {
  name: 'App',
  components: {
    
  },
  created() {
    document.querySelector('meta[name="theme-color"]').setAttribute('content',  this.$store.state.c1)
    this.$store.dispatch('set_is_in_game', false)
    // Confirm if really quiting the game
    this.$router.beforeEach((to, from, next) => {
      if (this.$store.state.is_in_game) {
        const answer = window.confirm('Voulez-vous vraiment quitter cette page ?')
        if (answer) {
          next()
        }
        else {
          next(false)
        }
      }
      else {
        next()
      }
    })
  }
}
</script>

<style>
html, body, #app {
  height: 100%;
}

#theapp {
  overflow: auto;
}

