<template>
    <div class="col">
        <div class="">
            <div class="my-2" :style="{'background-color': $store.state.c3}">
                <!-- <p class="text-center font-italic m-0">Rejoindre une partie</p> -->
                <p class="text-center font-italic m-0">
                    <strong>Votre pseudo : </strong> {{ pseudo }}
                </p>
            </div>
            <div class="form-group row">
                <div class="col-md-4"></div>
                <div class=" col-md-4">
                    <label for="game-code" :style="{'color': $store.state.c4}">Code de la partie</label>
                    <input type="text" class="form-control" id="game-code" autocomplete="off" v-model="code">
                </div>
                <div class="col-md-4"></div>
            </div>

            <div class="row d-flex justify-content-center">
                <div class="col-md-4">
                    <Btn class="btn-block" :c1="$store.state.c2" :c2="$store.state.c4" :text="'Confirmer'" :cl="'fa-solid fa-circle-check'" @click="click_confirm()" />
                    <Btn class="btn-block" :c1="$store.state.c2" :c2="$store.state.c4" :text="'Accueil'" :cl="'fa-solid fa-rotate-left'" @click="go_home()" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// import axios from 'axios'
import Btn from '@/components/elements/Btn.vue'

export default {
    components: {
        Btn
    },
    props: {
        pseudo: {
            type: String,
            default: "Joueur"
        }
    },
    data () {
        return {
            code: ''
        }
    },
    methods: {
        click_confirm() {
            let code = this.code.trim()
            this.code = code
            if (!code) {
                alert("Le code ne doit pas être vide")
            }
            else {
                this.$emit("confirm", code)
            }
        },
        go_home() {
            let data = {
                "mode": "select_mode"
            }
            this.$emit("mode", data)
        }
    },
}
</script>

