<template>
    <span class="picture_wrapper">
        <img class="picture" :src="get_src">
    </span> 
</template>

<script>
// import axios from 'axios'

export default {
    components: {
    },
    props: {
        for_what: {
            type: String,
            default: "win"
        }
    },
    data () {
        return {
            
        }
    },
    computed: {
        get_src() {
            let src = ""
            if (this.for_what == "win") {
                src = require('@/assets/img/awards/award_win.png')
            }
            else if (this.for_what == "lose") {
                src = require('@/assets/img/awards/award_poop.png')
            }
            return src
        }
    },
    methods: {

    },
    created() {
        
    }
}
</script>

<style scoped>

.picture_wrapper {
    height: 25px;
    width: 25px;
}

.picture {
    height: 25px;
    width: 25px;
    object-fit: cover;
}
</style>