<template>
    <div class="p-1 h-100">
        <div class="h-100">
            <IndicationBar :indication="'result_vote'" :round="round" :date_end="data_phase.date_end" @end="handle_end" />
            
            <div class="result-zone">
                <p v-if="data_phase.ranking.length > 0" class="m-0" :style="{'color': $store.state.c4}">
                    <ul class="">
                        <li v-if="!data_phase.winning_people.last" class="">
                            <span class="font-weight-bold" :style="{'color': $store.state.c3}">
                                {{data_phase.winning_people.firsts[0].pseudo}}
                            </span> 
                            gagne 
                            <span class="font-weight-bold" :style="{'color': $store.state.c3}">
                                {{data_phase.max_votes + 1}} points
                            </span>
                        </li>

                        <li v-else class="">
                            <span v-for="person, i in data_phase.winning_people.firsts" :key="i">
                                <span class="font-weight-bold" :style="{'color': $store.state.c3}">{{person.pseudo}}</span>{{i == data_phase.winning_people.firsts.length-1 ? ' et ' : ', '}}
                            </span>
                            <span class="font-weight-bold" :style="{'color': $store.state.c3}">{{data_phase.winning_people.last.pseudo}}</span>
                            gagnent 
                            <span class="font-weight-bold" :style="{'color': $store.state.c3}">
                                {{data_phase.max_votes + 1}} points
                            </span>
                        </li>

                        <li class="">
                            Tous ceux qui ont voté pour {{data_phase.winning_people.last ? "leurs réponses" : "sa réponse"}} gagnent 
                            <span class="font-weight-bold" :style="{'color': $store.state.c3}">
                                {{data_phase.max_votes}} {{data_phase.max_votes > 1 ? "points" : "point"}}
                            </span>
                        </li>
                    </ul>
                </p>

                <div class="" :style="{'color': $store.state.c4}">
                    <p class="m-0 text-center">{{q_a.question}}</p>
                    <p class="m-0 text-center">La réponse <span class="font-weight-bold">{{q_a.answer}}</span> :</p>
                </div>

                <div class="">
                    <div v-for="person, i in data_phase.ranking" :key="i" class="d-flex justify-content-between my-2 rounded" :style="{'border': '2px solid '+$store.state.c4}">
                        <div class="d-flex justify-content-center align-items-center result-row-rank font-weight-bold" :style="{'color': $store.state.c1, 'background-color': $store.state.c4}">
                            <span>{{ '#'+(i+1) }}</span>
                        </div>
                        <div class="result-row-answer">
                            <p class="m-0 font-weight-bold px-1" :style="{'color': $store.state.c3}">
                                {{person.pseudo}} <span class="font-italic">({{person.nb_votes}} {{person.nb_votes > 1 ? "votes" : "vote"}})</span>
                            </p>
                            <p class="m-0 pb-1 px-1" :style="{'color': $store.state.c4}">{{person.answer}}</p>
                        </div>
                        <div class="d-flex justify-content-center align-items-center result-row-points font-weight-bold" :style="{'color': $store.state.cpoints}">
                            <span v-if="person.points > 0">+{{person.points}} {{person.points > 1 ? "points" : "point"}}</span>
                        </div>
                    </div>
                </div>

                <div v-if="data_phase.ranking.length == 0" class="mt-2">
                    <p class="text-center m-0 font-weight-bold" :style="{'color': $store.state.cdanger}">
                        Personne n'a voté <i class="fa-regular fa-face-frown"></i>
                    </p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// import axios from 'axios'
// import Btn from '@/components/elements/Btn.vue'
import IndicationBar from '@/components/game/IndicationBar.vue'

export default {
    components: {
        IndicationBar,
        // Btn
    },
    props: {
        is_host: {
            type: Boolean,
            default: true
        },
        round: {
            type: Number,
            default: 1
        },
        data_phase: {
            default: {}
        },
        q_a: {
            default: {
                "question": "",
                "answer": ""
            }
        },
    },
    data () {
        return {
            
        }
    },
    methods: {
        handle_end() {
            if (this.data_phase.finished) {
                this.$emit('end', 'ranking')
            }
            else {
                this.$emit('end', 'loading_start')
            }
        }
    },
}
</script>

<style scoped>
.result-zone p, .result-zone li, .result-zone span {
    font-size: 14px;
}

.result-row-rank {
    width: 10%;
}

.result-row-answer {
    width: 65%;
}

.result-row-points {
    width: 25%;
}
</style>